.carousel-wrap .carousel__button {
  position: absolute;
  top: 50%; /* Center vertically */
  transform: translateY(-50%);
  background-color: #fff; /* Optional: Style for buttons */
  border: 1px solid #ccc;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10; /* Ensure buttons are above the carousel items */
}

.carousel-wrap .carousel__button--prev {
  left: -60px; /* Position the button outside the left edge */
}

.carousel-wrap .carousel__button--next {
